import React from 'react'
import {View, Text, StyleSheet, Dimensions, ImageBackground,Image, Button, TouchableOpacity} from 'react-native'
import PALM from './PALM.png'; import BANANA from './bananas.png'; import APPLE from './apple.png'; import PINEAPPLE from './pineapple.png'; import JACKFRUIT from './jackfruit.png'
import MANGO from './mango.png'
import BACK from './backArrow.png'
import {App} from './App'

const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

export class Palm extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			back: ' '
		};
	}

	goBack = () => {
		this.setState({ 
			back: 'yes'
		});
	}

	render() {
		if (this.state.back == 'yes') {
				return <App/>
			}	

		return (
			<View>

				<View style ={styles.container1}>
					<TouchableOpacity onPress = {this.goBack}>
		 				<ImageBackground source = {BACK} style = {styles.backButton} alt = "" resizeMode = 'contain' />
		 			</TouchableOpacity>
		 		</View>

				<View style ={styles.container}>
					<Text style = {styles.text}> PALM TREE </Text>
	 				<ImageBackground source = {PALM} style = {styles.tree} alt = "" resizeMode = 'contain'>
	 					<Image source = {BANANA} style = {styles.ban} alt = "" resizeMode = 'contain' />
	 					<Image source = {APPLE} style = {styles.appl} alt = "" resizeMode = 'contain' />
	 					<Image source = {PINEAPPLE} style = {styles.pine} alt = "" resizeMode = 'contain' />
	 					<Image source = {JACKFRUIT} style = {styles.jack} alt = "" resizeMode = 'contain' />
	 					<Image source = {MANGO} style = {styles.mang} alt = "" resizeMode = 'contain' />
	 				</ImageBackground>
				</View>

			</View>
		)
	}
}

const styles = StyleSheet.create({
	container: {
		justifyContent: 'center',
		alignItems: 'center',
	},

	container1: {
		flex:1,
		flexDirection: 'row',
		alignItems: 'center',
    	justifyContent: 'start',
	},

	backButton: {
		width: windowWidth * 0.1,
    	height: windowHeight * 0.1,
	},
	tree: {
   		width: windowWidth * 0.5,
    	height: windowHeight * 0.5,
	},
	
	ban: {
		width: windowWidth * 0.1,
    	height: windowHeight * 0.1,
    	top: '35%',
    	left: '80%',
	},

	appl: {
		width: windowWidth * 0.1,
		height: windowHeight * 0.1,
		top: '8%',
		right: '0%',
	},

	pine:{
		width: windowWidth * 0.15,
		height: windowHeight * 0.15,
		bottom: '45%',
		left: '12%',
	}, 

	jack:{
		width: windowWidth * 0.15,
		height: windowHeight * 0.15,
		bottom: '24%',
		left: '60%',
		transform: [{rotate: '90deg'}],
	}, 

	mang: {
		width: windowWidth * 0.1,
		height: windowHeight * 0.1,
		bottom: '72%',
		left: '35%',
	},
	text: {
		padding: 30,
		fontSize: 40,
		textAlign: 'center',
		color: 'green',
	},
	
})
