import React from 'react'
import {View, Text, StyleSheet, Dimensions, ImageBackground, Image, Button, TouchableOpacity} from 'react-native'
import OAK from './OAK.png'; import BACK from './backArrow.png'; import BANANA from './bananas.png'; import APPLE from './apple.png'; import PINEAPPLE from './pineapple.png'; import JACKFRUIT from './jackfruit.png'
import MANGO from './mango.png'
import {App} from './App'

const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

export class Oak extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			back: ' ',
			achievement_0: ' ',
			achievement_1: ' ',
			achievement_2: ' ',
			achievement_3: ' ',
			achievement_4: ' ',
			achievement_5: ' ',
		};
	}

	goBack = () => {
		this.setState({ 
			back: 'yes'
		});
	}

	renderFruit = () => {
		if (this.state.achievement_0 == 'complete'){
			return ( <Image source = {BANANA} style = {styles.ban} alt = "" resizeMode = 'contain' /> )
		} 
	}



	render() {
		if (this.state.back == 'yes') {
				return <App/>
			}	

		return (
			<View>

				<View style ={styles.container1}>
					<TouchableOpacity onPress = {this.goBack}>
		 				<ImageBackground source = {BACK} style = {styles.backButton} alt = "" resizeMode = 'contain' />
		 			</TouchableOpacity>
		 		</View>

				<View style ={styles.container}>
					<Text style = {styles.text}> OAK TREE </Text>
	 				<ImageBackground source = {OAK} style = {styles.tree} alt = "" resizeMode = 'contain'>
	 					<Image source = {BANANA} style = {styles.ban} alt = "" resizeMode = 'contain' />
	 					<Image source = {APPLE} style = {styles.appl} alt = "" resizeMode = 'contain' />
	 					<Image source = {PINEAPPLE} style = {styles.pine} alt = "" resizeMode = 'contain' />
	 					<Image source = {JACKFRUIT} style = {styles.jack} alt = "" resizeMode = 'contain' />
	 					<Image source = {MANGO} style = {styles.mang} alt = "" resizeMode = 'contain' />
	 				</ImageBackground>
				</View>

			</View>
		)
	}
}

